.Brand-Container{
    width: 100%;
    text-align: center;
    color: rgb(184, 26, 87);
}

.Brand-Container h1{    
    font-weight: 600;
}

.group {
    display: flex;
    line-height: 28px;
    align-items: center;
    position: relative;
    border-radius: 20px;
    overflow: hidden;
   }
   
   .input {
    width: 100%;
    height: 40px;
    line-height: 28px;
    padding: 0 1rem;
    padding-left: 2.5rem;
    border: 2px solid transparent;
    /* border-radius: 8px; */
    outline: none;
    background-color: #f3f3f4;
    color: #0d0c22;
    transition: .3s ease;
   }
   
   .input::placeholder {
    color: #9e9ea7;
   }
   
   .input:focus, input:hover {
    outline: none;
    border-top: 0;
    border-left: 0;
    border-right: 0;
    border-color: rgba(234,76,137,0.4);
    background-color: #fff;
    /* box-shadow: 0 0 0 4px rgb(234 76 137 / 10%); */
    
   }
   
   .icon {
    position: absolute;
    left: 1rem;
    fill: #9e9ea7;
    width: 1rem;
    height: 1rem;
   }
   
   .nav-list{
    width: 100%;
    display: flex;
    justify-content: space-evenly;
   }

   .nav-item{
    color: rgba(234,76,137);
    display: flex;
    font-size: 16px;
    }

    .Login-icon{
        font-size: 20px;
        margin-top: 3px;
        padding: 2px;
    }

